<template>
   <el-form ref="form" label-width="180px" label-position="left">
      <el-form-item label="">
         <el-checkbox v-model="checked"></el-checkbox>
      </el-form-item>
      <el-form-item label=":" v-if="checked">
         <el-select v-model="direction">
            <el-option label="" value="column"></el-option>
            <el-option label="" value="row"></el-option>
         </el-select>
      </el-form-item>
      <el-form-item label=" :" v-if="checked">
         <el-select v-model="wrap">
            <el-option label="" value="wrap"></el-option>
            <el-option label=" " value="nowrap"></el-option>
         </el-select>
      </el-form-item>
      <el-form-item label="  :" v-if="checked">
         <el-select v-model="justifyContent">
            <el-option label=" " value="flex-start"></el-option>
            <el-option label=" " value="flex-end"></el-option>
            <el-option label=" " value="center"></el-option>
            <el-option label=" " value="space-between"></el-option>
            <el-option label=" " value="space-around"></el-option>
            <el-option label=" " value="space-evenly"></el-option>
         </el-select>
      </el-form-item>
      <el-form-item label=" . :" v-if="checked">
         <el-select v-model="alignItems">
            <el-option label="" value="stretch"></el-option>
            <el-option label=" " value="flex-start"></el-option>
            <el-option label=" " value="flex-end"></el-option>
            <el-option label=" " value="center"></el-option>
            <el-option label=" " value="baseline"></el-option>
         </el-select>
      </el-form-item>
      <el-form-item label=". .:" v-if="checked">
         <el-select v-model="alignSelf" placeholder=" ">
            <el-option label="" value="auto"></el-option>
            <el-option label="" value="normal"></el-option>
            <el-option label=" " value="center"></el-option>
            <el-option label=" " value="flex-start"></el-option>
            <el-option label=" " value="flex-end"></el-option>
            <el-option label="" value="stretch"></el-option>
            <el-option label=" " value="baseline"></el-option>
         </el-select>
      </el-form-item>
      <el-form-item label=":" v-if="checked">
         <el-input-number v-model="shrink" :min="0"></el-input-number>
      </el-form-item>
      <el-form-item label=":" v-if="checked">
         <el-input-number v-model="grow" :min="0"></el-input-number>
      </el-form-item>
      <el-form-item label=". :" v-if="checked">
         <el-input-number v-model="basis" :min="0" :placeholder="'auto'"></el-input-number>
      </el-form-item>
   </el-form>
</template>

<script>
   export default {
      props: ['value'],
      data: ()=>({
         checked: false
      }),
      computed: {
         direction: {
            get(){
               return this.value.direction
            },
            set(v) {
               this.$emit('change', {direction: v})
            }
         },
         wrap: {
            get(){
               return this.value.wrap
            },
            set(v) {
               this.$emit('change', {wrap: v})
            }
         },
         justifyContent: {
            get(){
               return this.value.justifyContent
            },
            set(v) {
               this.$emit('change', {justifyContent: v})
            }
         },
         alignItems: {
            get(){
               return this.value.alignItems
            },
            set(v) {
               this.$emit('change', {alignItems: v})
            }
         },
         alignSelf: {
            get(){
               return this.value.alignSelf
            },
            set(v) {
               this.$emit('change', {alignSelf: v})
            }
         },
         shrink: {
            get(){
               return this.value.shrink
            },
            set(v) {
               this.$emit('change', {shrink: v})
            }
         },
         grow: {
            get(){
               return this.value.grow
            },
            set(v) {
               this.$emit('change', {grow: v})
            }
         },
         basis: {
            get(){
               return this.value.basis
            },
            set(v) {
               this.$emit('change', {basis: v})
            }
         },
      }
   }
</script>