<template>
   <form>
      <hr>
      <label>
         : 
      <select 
         :value="value.family"
         @change="$emit('change', {family: $event.target.value})"
      >
         <option value="inherit"></option>
         <option value="Arial">Arial</option>
         <option value="New Roman">New Roman</option>
      </select>
      </label>
      <br>
      <label>
         : 
      <select 
         :value="value.style"
         @change="$emit('change', {style: $event.target.value})"
      >
         <option value="inherit"></option>
         <option value="normal"></option>
         <option value="italic"></option>
      </select>
      </label>
      <br>
      <label>
         : 
      <select 
         :value="value.weight"
         @change="$emit('change', {weight: $event.target.value})"
      >
         <option value="inherit"></option>
         <option value="normal"></option>
         <option value="bold"></option>
      </select>
      </label>
      <br>
      <label>
         :
         <input 
            type="number"
            :value="size"
            @change="$emit('change', {size: $event.target.value + 'px'})"
            placeholder=""
         >
      </label>
      <br>
      <label>
         : 
         <input 
            type="color" 
            :value="color" 
            @change="$emit('change', {color: $event.target.value})"
         >
      </label>
      <br>
      <label>
         : 
      <select 
         :value="value.register"
         @change="$emit('change', {register: $event.target.value})"
      >
         <option value="inherit"></option>
         <option value="capitalize"> </option>
         <option value="uppercase">  </option>
         <option value="lowercase">  </option>
         <option value="none"> </option>
      </select>
      </label>
      <br>
   </form>
</template>

<script>
export default {
   props: ['value'],
   data:()=>({
      font: {},
   }),
   computed:{
      size(){
         let size;
         if(this.value.size==='inherit'){
            size = '';
         } else 
            size = parseFloat(this.value.size.replace(/[^.0-9]/gim,''));
         return size
      },
      color(){
         let some;
         if(this.value.color==='inherit'){
            some = '';
         } else 
            some = this.value.color;
         return some;
      },
   }
}
</script>